    margin-bottom: 20px;
  }
}
header .headerTitle .headerLogin form {
  text-align: center;
}
header .headerTitle .headerLogin form input {
  width: 300px;
  margin: 20px 0px;
  padding: 10px 20px;
  border-radius: 3px;
  border: 2px solid black;
  background-color: transparent;
  color: black;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 1px;
}
header .headerTitle .headerLogin form input:focus {
  outline: none;
}
header .headerTitle .headerLogin form input::placeholder {
  color: black;
  text-transform: capitalize;
  font-size: 13px;
  letter-spacing: 2px;
}
header .headerTitle .headerLogin form .pass {
  position: relative;
}
header .headerTitle .headerLogin form .pass .fa {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  color: black;
  font-size: 14px;
}
header .headerTitle .headerLogin form button {
  padding: 10px 30px;
  color: black;
  background-color: transparent;
  outline: 2px solid black;
  border: none;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 2px;
  border-radius: 2px;
  overflow: hidden;
  position: relative;
}
header .headerTitle .headerLogin form button::after {
  content: "";
  display: block;
  width: 50%;
  height: 100%;
  position: absolute;
  top: 0px;
  right: 0px;
  background-color: #01285f;
  transform: translateX(100px);
  z-index: -11;
  color: white;
}
header .headerTitle .headerLogin form button::before {
  content: "";
  display: block;
  width: 50%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: #01285f;
  transform: translateX(-100px);
  z-index: -11;
  color: white;
}
header .headerTitle .headerLogin form button:hover::after {
  transition: 1s;
  cursor: pointer;
  transform: translateX(0px);
  background-color: #01285f;
  color: white;
}
header .headerTitle .headerLogin form button:hover::before {
  transition: 1s;
  cursor: pointer;
  transform: translateX(0px);
  background-color: #01285f;
  color: white;
}
header .headerTitle .headerLogin .close {
  position: absolute;
  top: 10px;
  right: 30px;
  display: block;
  border: 2px solid #01285f;
  padding: 15px 20px;
  color: black;
  border-radius: 5px;
  font-size: 18px;
  transition: 0.3s;
  cursor: pointer;
}
header .headerTitle .headerLogin .close:hover {
  background-color: black;
  color: red;
  transform: rotate(180deg);
}
